@import url(https://fonts.googleapis.com/css2?family=Yanone+Kaffeesatz:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
  background: #f7f7f8;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background-color: var(--background);
  color: var(--text);
}
html {
  scroll-behavior: smooth;
}

.intro{
  margin: 10px 0 0 0;
  padding: 0 20px 0 20px;
  height: 100vh;
}

.nameHeader{
    font-size: 7em;
    font-weight: 700;
    text-align: justify;
}

.subHeaderIntro{
  font-size: 1.8em;
  font-weight: 400;
  font-style: italic;
  font-feature-settings: normal;
  font-variant: normal;
  text-align: center;
  color: 	#007c87;
}

/* Media Query for Mobile Devices */
@media (max-width: 480px) {
  .intro { 
    height: 100vh;
  }

  .nameHeader{
    font-size: 5em;
  }

  .subHeaderIntro{
    font-size: 1.5em;
  }
}

/* Media Query for low resolution  Tablets, Ipads */
@media (min-width: 481px) and (max-width: 767px) {

  .intro { 
    height: 100vh;
  }

  .nameHeader{
    font-size: 5em;
  }

  .subHeaderIntro{
    font-size: 1.5em;
  }
}

@media (min-width: 768px) and (max-width: 991px) {
  .intro { 
    height: 100vh;
  }

  .nameHeader{
    font-size: 5em;
  }

  .subHeaderIntro{
    font-size: 1.5em;
  }
}
.section_style{
    padding: 25px 0 0 0;
}

.about_text{
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    letter-spacing: 0.00938em;
    text-align: justify;
}

.tool{
    font-weight: 200;
    
}

.tool_text{
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    letter-spacing: 0.00938em;
    text-align: center;
}

.icon{
    height: 75px;
    width: 75px;
}

.icon_credit{
    font-size: 0.5em;
}
.projectIcon {
  height: 1.7vw;
  width: 1.7vw;
  margin-left: 0.1vw;
  margin-right: 0.1vw;
}

.card-img-top {
  max-width: 100%;
  height: 100%;
  max-height: 100%;
}

.card-title {
  margin: 0.2vw;
  margin-bottom: 0.4vw;
  font-size: 1.5vw;
  font-weight: bold;
}

.card-text {
  text-align: center;
  height: 12vw;
  overflow: scroll;
  /* font-family: Roboto, Helvetica, Arial, sans-serif; */
  letter-spacing: 0.07em;
}

.project-table {
  background: #f7f7f8;
  border: 1px solid black;
  overflow: hidden;
  position: relative;
  border-radius: 2vw;
  margin: 2vw;
  padding: 1vw;
  color: black;
}

.project-image-td {
  height: 100%;
  width: 50%;
  position: absolute;
  right: 0;
  left: 0;
}

.project-intro-text{
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  letter-spacing: 0.00938em;
  text-align: center;
}

/* Media Query for Mobile Devices */
@media (max-width: 480px) {

  .projectIcon {
    height: 4.7vw;
    width: 4.7vw;
    margin-left: 1vw;
    margin-right: 1vw;
  }

  .card-title {
    font-size: 5vw;
  }

  .card-text {
    height: 20vw;
  }

  .project-table {
    border-radius: 5vw;

    /* (top, right, bottom, and left). */
    margin: 2.5vw 10vw 2.5vw 10vw;
  }

  .project-intro-text{
    margin: auto 2.5vw auto 2.5vw;
  }

}

/* Media Query for low resolution  Tablets, Ipads */
@media (min-width: 481px) and (max-width: 767px) {

  .projectIcon {
    height: 4.7vw;
    width: 4.7vw;
    margin-left: 1vw;
    margin-right: 1vw;
  }

  .card-title {
    font-size: 5vw;
  }

  .card-text {
    height: 20vw;
  }

  .project-table {
    border-radius: 5vw;

    /* (top, right, bottom, and left). */
    margin: 2.5vw 10vw 2.5vw 10vw;
  }

  .project-intro-text{
    margin: auto 2.5vw auto 2.5vw;
  }
  
}

/* Media Query for Tablets Ipads portrait mode */
@media (min-width: 768px) and (max-width: 990px) {

  .projectIcon {
    height: 4.7vw;
    width: 4.7vw;
    margin-left: 1vw;
    margin-right: 1vw;
  }

  .card-title {
    font-size: 5vw;
  }

  .card-text {
    height: 20vw;
  }

  .project-table {
    border-radius: 5vw;

    /* (top, right, bottom, and left). */
    margin: 2.5vw 10vw 2.5vw 10vw;
  }

  .project-intro-text{
    margin: auto 2.5vw auto 2.5vw;
  }
  
}

